@import url("https://fonts.googleapis.com/css?family=Source+Code+Pro:400,500,500i,600i,700,700i,900&display=swap");
body {
  font-family: "Source Code Pro", monospace;
}

.app {
  height: 100vh;
}

.flex {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.header {
  font-size: x-large;
  text-align: center;
}

.logo {
  padding: 20px;
}

.quote_card {
  width: 80%;
  padding: 20px 18px;
  margin: 30px auto;
  border: 2px solid;
  border-radius: 8px;
  line-height: 1.8;
  font-size: 18px;
}

.quote_author {
  text-align: right;
  font-size: smaller;
  font-style: italic;
}

.footer {
  padding: 20px;
  text-align: center;
  font-size: large;
}
